<template>
    <v-container>
        <v-row style="margin-left: 10px; margin-right: 10px">
            <v-col md=3 style="justify-content: center; text-align: center;">
                <v-avatar size="200">
                    <v-img class="card-img" :src="'https://library-site.s3.eu-north-1.amazonaws.com/avatars/' + readers.avatar"></v-img>
                </v-avatar>

                <v-form
                    ref="form"
                    v-model="valid"
                >
                <div class="upload" style="padding-top: 15px" v-if="this.$route.params.user_id.toString() === this.authId.toString()">
                    <v-form ref="form" v-model="form1" lazy-validation style="padding-bottom: 10px">
                      <v-file-input
                          v-model="avatar"
                          accept="image/png, image/jpeg, image/bmp"
                          label="Wybierz avatar"
                          :rules="photoRules"
                          outlined
                          dense
                          prepend-icon=""
                          prepend-inner-icon="mdi-camera"
                          hide-details
                        >
                        </v-file-input>
                        <v-btn
                          color="success"
                          rounded
                          block
                          class="mt-3"
                          @click="validate"
                          :disabled="!form1"
                        >
                          Zmień avatar
                        </v-btn>
                    </v-form>
                </div>
                </v-form>

            </v-col>
            <v-col style="text-align: left">
                <h2 style="padding-bottom: 10px">{{readers.name + ' ' + readers.surname}}</h2>
                <v-divider></v-divider>
                <p style="padding-top: 10px">Email: <span style="font-weight: bold; color: #008D18">{{readers.email}}</span></p>
                <p>Członek biblioteki od: <span style="font-weight: bold; color: #008D18">{{readers.created_at.slice(0,10)}}</span></p>
                <p>Numer karty czytelniczej: <span style="font-weight: bold; color: #008D18">{{readers.readers.card_number}}</span></p>
                <p v-if="readers.readers.can_extend === false">Termin przedłużenia książki: <span style="font-weight: bold; color: red">WYKORZYSTANO</span></p>
                <p v-else>Termin przedłużenia książki: <span style="font-weight: bold; color: #008D18">MOŻNA PRZEDŁUŻYĆ</span></p>
            </v-col>
        </v-row>

        <v-divider></v-divider>

        <v-col style="justify-content: center;" v-if="this.$route.params.user_id.toString() === this.authId.toString()">
            <ProfileTabs v-bind:user_id="user_id"/>
        </v-col>

    </v-container>
</template>

<script>
/*eslint-disable*/
import ProfileTabs from './ProfileTabs.vue';

export default {
  name: 'Profile',
  props: ['user_id'],
  components: {
    ProfileTabs
  },

  data: () => ({
    defaultButtonText: 'Wgraj awatar',
    avatar: null,
    valid: false,
    form1: false,
    photoRules: [v => !v || v.size < 2000000 || 'Zdjęcie powinno mieć poniżej 5MB!'],
  }),

  computed: {
    readers() {
      return this.$store.getters.getReaders;
    },
    buttonText() {
      return this.selectedFile ? this.selectedFile.name : this.defaultButtonText
    },
    authId() {
      return this.$store.getters.authId;
    }
  },

  created () {
    this.$store.dispatch('fetchOneReader', this.user_id);
  },

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        const blob = this.avatar
        const formData = new FormData();
        formData.append('avatar', blob)
        formData.append('user_id', parseInt(this.user_id));

        axios.post('/api/user/profile/upload', formData)
          .then((response) => {
            if (response.data.success == true) {
              this.$swal('Zmieniono', 'Pomyślnie zmieniono awatar!', 'success');
            } else {
              this.$swal('Błąd', 'Zmiana awataru zakończyła się niepowodzeniem!', 'error');
            }
          })
          .catch((error) => {
            console.log(error);
          });
          this.$store.dispatch('fetchOneReader', this.user_id);
      }
    }
  }
};
</script>
